/*全局样式*/
html body {
    background-color: #ffffff;
    padding: 0;
    margin: 0;
}
.container_Box {
    display: flex;
    height: 100vh;
    overflow: hidden;
    padding: 0;
    margin: 0;
}
.container_Box .one ul > li div h4, .container_Box .two ul > li div h4 {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.one .list {
    cursor: pointer;
}

.active {
    border-radius: 8px;
    background-color: #ff9f62;
}

#activeFont {
    color: #fffef6;
}

#commonFont {
    color: #222222;
}

.container_Box .two, .container_Box .one {
    display: inline-block;
    height: 100vh;
    overflow-y: scroll;
    transition: width .1s linear;
    position: relative;
    margin-top: 15px;
}
.container_Box .one {
    width: 15vw;
    padding: 20px;
    border-left: 1px solid #dddddd;
}

.container_Box .two {
    width: 0;
    padding: 0;
}

.container_Box .main {
    display: inline-block;
    flex: 1;
    height: 100vh;
    overflow-y: scroll;
    border-left: 1px solid #dddddd;
    padding: 20px;
    position: relative;
}

.title {
    cursor: pointer;
}

/*公共样式*/
.ant-spin-container .ant-list-items .ant-list-item {
    padding: 6px;
}

.ant-list .ant-spin-container .ant-list-item {
    padding: 3px;
}

.ant-list-item .ant-list-item-meta .ant-list-item-meta-content .ant-list-item-meta-title {
    padding: 0;
    margin: 0;
}
/*图标*/

.layerIcon {
    position: absolute;
    left: 0;
    right: 0;
    top: 10px;
    display: flex;
}

.zoom {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 95vw;
    transition: all .3s linear;
}

.one_hide_show {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 10px;
    transition: all .3s linear;
}

.two_hide_show {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 45px;
    transition: all .3s linear;
}
.ReceivingCode {
    position: fixed;
    z-index: 9999;
    bottom: 20px;
    right: 10px;
    width: 150px;
    border: 10px solid #d7d7f3;
    border-radius: 8px;
    cursor: pointer;
}
.ReceivingCode img {
    width: 100%;
}
.ReceivingCode .closeQr {
    width: 25px;
    position: absolute;
    left: -15px;
    top: -15px;
}
p>code {
    background-color: #f3f3f3;
    color: #da702b;
    border-radius: 4px;
    padding: 2px;
}
blockquote {
    border-left: 8px solid #488261;
    background-color: #eef7f2;
    border-radius: 4px;
    padding: 10px 6px;
}
blockquote p {
    margin-bottom: 0;
}

.ValineContainer {
    margin-top: 40px;
}
.Valine {
    margin-top: 30px;
}
